export default {
  name: 'user-info',
  data () {
    return {}
  },
  computed: {
    backUrl () {
      return 'url(' + this.img + ')'
    },
    img () {
      return this.$store.state.user.infoObj.stuHeadimgurl || this.$store.state.user.infoObj.teaHeadimgurl
    },
    hasInfo () {
      return this.$store.state.user.hasInfo
    }
  },
  methods: {
    gotoInfo () {
      if (this.hasInfo) {
        this.$router.push({ name: 'userInfo' })
      } else {
        this.gotoReg()
      }
    },
    gotoReg () {
      this.$router.push({ name: 'register' })
    }
  }
}



